/** @format */

import React, { useEffect } from "react";
import { useParams } from "react-router-dom";
import { Routes, Route } from "react-router-dom";
import { connect } from "react-redux";
import { actions } from "react-redux-form";

import Header from "./HeaderComponent";
import Footer from "./FooterComponent";
import Menu from "./MenuComponent";
import Contact from "./ContactComponent";
import DishDetail from "./DishDetailComponent";
import Home from "./HomeComponent";
import About from "./AboutComponent";
import {
	fetchDishes,
	fetchComments,
	fetchPromos,
	fetchLeaders,
	postComment,
} from "../redux/actions/actionCreators";

// Todo : hm ny nh ngha phn d liu t kho chung, ci m component ny mun ly v map chng nh l cc key ca props truyn vo component , iu ny cho php chng ta ch ly phn d liu chng ta cn t store , v ch khi phn d liu  thay i th component ca chng ta mi b re-render
const mapStateToProps = (state) => {
	return {
		dishes: state.dishes,
		comments: state.comments,
		promotions: state.promotions,
		leaders: state.leaders,
	};
};

// Todo : hm ny nh ngha s nh  ngha cc trng hp dispatch t server v a n vo Component thng qua props truyn vo, iu ny c c nh vo hm connect t react-redux
function mapDispatchToProps(dispatch) {
	return {
		postCommentProp: (comment) => {
			dispatch(postComment(comment));
		},
		fetchDishesProp: () => {
			dispatch(fetchDishes());
		},
		fetchCommentsProp: () => {
			dispatch(fetchComments());
		},
		fetchPromosProp: () => {
			dispatch(fetchPromos());
		},
		fetchLeadersProp: () => {
			dispatch(fetchLeaders());
		},
		resetFeedbackForm: () => {
			dispatch(actions.reset("feedback"));
		},
	};
}

function Main(props) {
	// Todo : Ln u khi Main c mount vo DOM th hm trong i s Effect ca useEffect s c gi(side Effect) Hm ny chy s chy hm fetchDishes()(l mt Thunk creator) hm ny chy s tr v mt thunk action(l mt hm vi i s th nht l dispatch) trong hm ny chng ta s gi li dispatch theo tng thi im khc nhau  dispatch cc action khc nhau ti store

	// Todo : Main c mount vo DOM , useEffect c chy,props.fetchDishesProp() c chy , sau   dispatch(fetchDishes()) c chy , trc  fetchDishes() chy tr v hm:
	// (dispatch) => {
	// 	dispatch(dishesLoading(true));

	// 	setTimeout(() => {
	// 		dispatch(addDishes(DISHES));
	// 	}, 2000);
	// };
	// Todo : hm ny l mt thunkAction , vic import thunk t redux-thunk cho php chng ta tuyn vo i s ca hm dispatch l mt hm thay v ch truyn vo mt object thun ty , hm chng ta truyn vo s c i s th nht l hm dispatch(l hm dispatch t react-redux) v i s th 2 l hm getstate dng  ly ra state hin ti ca store ,trong HM ThunkAction ny s x l cc logic bt ng b trc khi tht s dispatch mt action , hoc chungs ta c th dispatch nhiu action ti store

	useEffect(() => {
		props.fetchDishesProp();
		props.fetchCommentsProp();
		props.fetchPromosProp();
		props.fetchLeadersProp();
	}, []);

	const DishWithId = () => {
		const match = useParams();
		return (
			<DishDetail
				isLoading={props.dishes.isLoading}
				errorMsg={props.dishes.errorMsg}
				selectedDish={
					props.dishes.dishes.filter((dish) => {
						return dish.id === parseInt(match.dishID, 10);
					})[0]
				}
				postComment={props.postCommentProp}
				comments={props.comments.comments.filter((comment) => {
					return comment.dishId === parseInt(match.dishID, 10);
				})}
				commentsErrorMsg={props.comments.errorMsg}
				totalNumberOfComments={props.comments.comments.length}
			/>
		);
	};

	return (
		<div>
			<div className="container">
				<Header />
				<Routes>
					<Route
						path="/menu"
						exact
						element={
							<Menu
								dishesLoading={props.dishes.isLoading}
								dishesErrorMsg={props.dishes.errorMsg}
								dishes={props.dishes.dishes}
							/>
						}
					/>
					<Route
						path="/home"
						element={
							<Home
								dishesLoading={props.dishes.isLoading}
								dishesErrorMsg={props.dishes.errorMsg}
								dish={props.dishes.dishes.filter((dish) => dish.featured)[0]}
								promosLoading={props.promotions.isLoading}
								promosErrorMsg={props.promotions.errorMsg}
								promotion={props.promotions.promotions.filter((pro) => pro.featured)[0]}
								leader={props.leaders.leaders.filter((leader) => leader.featured)[0]}
								leadersLoading={props.leaders.isLoading}
								leadersErrorMsg={props.leaders.errorMsg}
							/>
						}
					/>
					<Route exact path="/menu" element={<Menu dishes={props.dishes} />} />
					<Route
						index
						element={
							<Home
								dishesLoading={props.dishes.isLoading}
								dishesErrorMsg={props.dishes.errorMsg}
								dish={props.dishes.dishes.filter((dish) => dish.featured)[0]}
								promosLoading={props.promotions.isLoading}
								promosErrorMsg={props.promotions.errorMsg}
								promotion={props.promotions.promotions.filter((pro) => pro.featured)[0]}
								leader={props.leaders.leaders.filter((leader) => leader.featured)[0]}
								leadersLoading={props.leaders.isLoading}
								leadersErrorMsg={props.leaders.errorMsg}
							/>
						}
					/>
					<Route
						exact
						path="/contactus"
						element={<Contact resetFeedbackForm={props.resetFeedbackForm} />}
					/>
					<Route path="/menu/:dishID" element={<DishWithId />}></Route>
					<Route path="/aboutus" element={<About leaders={props.leaders.leaders} />} />
				</Routes>
				<Footer />
			</div>
		</div>
	);
}

export default connect(mapStateToProps, mapDispatchToProps)(Main);



