/** @format */
import { combineReducers } from "redux";
import { createForms } from "react-redux-form";

import { CommentsReducer } from "./CommentsReducer";
import { DishesReducer } from "./DishesReducer";
import { PromotionsReducer } from "./PromotionsReducer";
import { LeadersReducer } from "./LeadersReducer";
import { initialFeedback } from "./form";
// Todo : createFrom l mt reducer qun l d liu t form ca react-redux-form bn trong redux-store

export const rootReducer = combineReducers({
	dishes: DishesReducer,
	comments: CommentsReducer,
	promotions: PromotionsReducer,
	// Todo: createForm s to ra mt state vi field l feedback v gi tr khi to ininialFeedback , state ny c qun l bi logic bn trong creatForm, bn trong n s c Reducer  qun l d liu ca form
	leaders: LeadersReducer,
	...createForms({ feedback: initialFeedback }),
});



