/** @format */

import * as actionType from "./actionTypes";

import { baseUrl } from "../../shared/baseUrl";

//Todo : Hm ny l mt ActionCreator, n tr v mt Action {Plain Object key ln lt l type v payload}

export const addComment = (comment) => ({
	type: actionType.ADD_COMMENT,
	payload: comment,
});

export const postComment = (comment) => (dispatch) => {
	return fetch(baseUrl + "comments", {
		method: "POST",
		body: JSON.stringify(comment),
		headers: {
			"Content-Type": "application/json",
		},
		credentials: "same-origin",
	})
		.then(
			(response) => {
				if (response.ok) {
					return response;
				} else {
					var error = new Error("Error " + response.status + ": " + response.statusText);
					error.response = response;
					throw error;
				}
			},
			(error) => {
				throw error;
			}
		)
		.then((response) => response.json())
		.then((response) => dispatch(addComment(response)))
		.catch((error) => {
			console.log("post comments", error.message);
			alert("Your comment could not be posted\nError: " + error.message);
		});
};

//Todo : Hm ny l mt ThunkActionCreator, n tr v mt Thunk Action, l mt hm vi i s hm dispatch
export const fetchDishes = () => (dispatch) => {
	dispatch(dishesLoading());

	fetch(baseUrl + "dishes")
		.then(
			(res) => {
				if (res.ok) {
					return res;
				} else {
					var err = new Error("Error" + res.status + ":" + res.statusText);
					err.res = res;
					throw err;
				}
			},
			(error) => {
				var errorMess = new Error(error.message);
				throw errorMess;
			}
		)
		.then((res) => res.json())
		.then((dishes) => dispatch(addDishes(dishes)))
		.catch((error) => {
			dispatch(dishesFailed(error.message));
		});
};

//Todo : Hm ny l mt ActionCreator, n tr v mt Action {Plain Object key ln lt l type v payload, action ny khng c payload m n ch c type action v n khng c nhim v lm thay i store ca redux }
