/** @format */

import { createStore, applyMiddleware } from "redux";
import thunk from "redux-thunk";
import logger from "redux-logger";

import { rootReducer } from "./reducer";

// TODO : Vic apply thunk middleware cho php chng ta truyn vo hm dispatch mt hm (thunk_Action ) thay v ch truyn mt object , trong hm ny c mt i s truyn vo l dispatch v trong phn inh ngha ca hm chng ta c th x l logic bt ng b sau  mi thc s dispatch action (c th l kt qu tr v t nhng logic bt ng b nh get d liu t server )

export const ConfigureStore = () => {
	const store = createStore(rootReducer, applyMiddleware(thunk, logger));
	return store;
};



